<template>
    <el-dialog
            :title="title"
            :visible.sync="dialogVisible"
            width="440px"
            class="general__dialog"
            :before-close="handleClose">

        <el-form :model="ruleForm"
                 :rules="rules"
                 ref="ruleForm"
                 label-width="85px"
                 hide-required-asterisk
                 class="demo-ruleForm">
            <el-form-item label="项目名称" prop="address">
                <el-input v-model="ruleForm.address"
                          size="small"
                          placeholder="请输入设备地址"
                ></el-input>
            </el-form-item>

            <el-form-item label="项目编号" prop="name">
                <el-input v-model="ruleForm.name"
                          size="small"
                          placeholder=""
                ></el-input>
            </el-form-item>


            <el-form-item label="中心点" prop="center">
                <el-input v-model="ruleForm.center"
                          size="small"
                          placeholder=""
                ></el-input>
                <i
                        class="iconfont icon-zuobiao zuobiao"
                        @click="coordinateCenterPointVisible = true"
                ></i>
            </el-form-item>

            <el-form-item label="坐标集" prop="axio">
                <el-input v-model="ruleForm.axio"
                          size="small"
                          placeholder=""
                ></el-input>
                <i
                        class="iconfont icon-ditu ditu"
                        @click="coordinateDialogVisible = true"
                ></i>
            </el-form-item>

            <el-form-item label="说明" prop="desc" class="bottom-solid">
                <el-input type="textarea" v-model="ruleForm.desc"></el-input>
            </el-form-item>

            <el-form-item style="text-align: right">
                <el-button type="primary" @click="submitForm('ruleForm')" size="small">确 定</el-button>
                <el-button @click="$emit('dialogVisibleStatus',false)" size="small">取 消</el-button>
            </el-form-item>

        </el-form>
        <TempCoordinateDialog
            @coordinateDialogVisibleStatus="getCoordinateStatus"
            :coordinateDialogVisible="coordinateDialogVisible"
            @coodinateSet="getcoodinateSet"
            title="选择坐标集"
            :coordinateList = 'coordinateList.join(";")'
        >

        </TempCoordinateDialog>
        <TempCoordinateCenterPoint
            @coordCPVStatus="getCoordCPVStatus"
            @coodData = "getCoodData"
            :coordinateCenterPointVisible="coordinateCenterPointVisible"
            title="选择中心点"
            :east = "east"
            :north = "north"
        >
        </TempCoordinateCenterPoint>
    </el-dialog>
</template>
<script>
    import TempCoordinateDialog   from './CoordinateDialog.vue'
    import TempCoordinateCenterPoint   from './CoordinateCenterPointDialog.vue'
    export default {
        components: {
            TempCoordinateDialog,
            TempCoordinateCenterPoint
        },
        data() {
            return {
                status:false,
                coordinateDialogVisible:false,
                coordinateCenterPointVisible:false,
                ruleForm: {
                    address: '',
                    name:'',
                    region:'',
                    project:'',
                    axio:'',
                    center:'',
                    desc:''
                },
                rules: {
                    address: [
                        { required: true, message: '请输入项目名称', trigger: 'change' },
                    ],
                    name:[
                        { required: true, message: '请输入项目编号', trigger: 'change' },
                    ],
                    center:[
                        { required: true, message: '请输入中心点', trigger: 'change' }
                    ],
                    axio:[
                        { required: true, message: '请输入坐标集', trigger: 'change' }
                    ],
                    desc: [
                        { required: true, message: '请填写说明内容', trigger: 'blur' }
                    ]
                },
                east:'',
                north:'',
                coordinateList:[]
            }
        },
        props:{
            dialogVisible:Boolean,
            title:{
                type:String
            }
        },
        watch:{
//            dialogVisible
        },
        mounted(){

        },
        methods:{
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        alert('submit!');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            getCoodData(data){
                this.ruleForm.center = `东经${data.x} , 北纬${data.y}`;
                this.east = data.x;
                this.north = data.y;
            },
            getcoodinateSet(data){
                let nArray = [];
                this.coordinateList = data;
                data.forEach((item,index) => {
                    nArray.push(`东经${item[0]} , 北纬${item[1]}`);
                });
                this.ruleForm.axio = nArray.join(';');
            },
            resetForm(formName) {
                this.$refs[''].resetFields();
            },
            handleClose(done){
                this.$refs['ruleForm'].resetFields();
                this.$emit('dialogVisibleStatus',false);
            },
            getCoordinateStatus(){
                this.coordinateDialogVisible = false
            },
            getCoordCPVStatus(){
                this.coordinateCenterPointVisible = false
            }
        },
    }
</script>
<style lang="scss" scoped>
    @import "../../../assets/scss/_mixin.scss";
    .zuobiao,.ditu{
        color: $--color-primary;
        margin-left: 5px;
        font-size: 18px;
        cursor: pointer;
    }
</style>