<template>
  <Layout class="scale_screen">
    <Sider hide-trigger>
      <TempSider
      :isCollapse = 'isCollapse'
      ></TempSider>
    </Sider>
    <Layout>
      <Header
      >
        <TempHeader
          @setCollapseStatus = 'getCollapseStatus'
        >

        </TempHeader>
      </Header>
      <Content>
        <TempProjectTag></TempProjectTag>
        <router-view></router-view>
      </Content>
      <!--<Footer>Footer</Footer>-->
    </Layout>
  </Layout>
</template>

<script>
  import TempSider  from './Sider.vue'
  import TempHeader   from './Header.vue'
  import TempProjectTag   from '../project/list/ProjectTag.vue'
  export default {
    name: 'app',
    data(){
      return{
        phone: 'apple',
        isCollapse:false,
      }
    },
    mounted(){

    },
    methods:{
      resizeFunc(){
          let vm = this;
//          vm.setZoom();     //初始化触发
          /**
           * 当浏览器窗口大小发生变化时触发
           */
          $(window).bind('resize',function(){
              vm.setZoom();
          });
      },
      setZoom(obj){
          obj = obj||$('.scale_screen');
          var w = $(window).width();
          var h = $(window).height();
          var maxWidth = 1920;
          var maxHeight = 1080;
          window.zoom = 1;
          if(h>w+150){
              // zoom = w/maxWidth;
              // obj.css({zoom:zoom});
              // $(".aside").css({zoom:zoom});
          }else{
              zoom = h/maxHeight;
              if(w/h>3&&w/maxWidth>2){
                  zoom += 0.1;
              }
              obj.css({zoom:zoom});
              // $(".aside").css({zoom:zoom});
          }
      },
      getCollapseStatus(data){
        this.isCollapse = data
      }
    },
    components: {
      TempSider,
      TempHeader,
      TempProjectTag
    }
  }
</script>

