<template>
    <el-dialog
            :title="title"
            :visible.sync="coordinateDialogVisible"
            width="100%"
            class="general__dialog"
            top="5vh"
            append-to-body
            :before-close="handleClose">
        <div class="amap-page-head" style="">
            <div class="search-box_wrap">
                <el-amap-search-box
                        class="search-box"
                        :search-option="searchOption"
                        :on-search-result="onSearchResult">
                </el-amap-search-box>
                <el-button type="primary" plain @click="polygons[0].path = [];coordinateSet = [];">删除坐标集</el-button>
            </div>
        </div>
        <div class="amap-page-container">
            <el-amap
                ref="map"
                vid="amapDemo"
                :amap-manager="amapManager"
                :center="center" :zoom="zoom"
                :plugin="plugin"
                :events="events"
                class="amap-demo"
            >
                <el-amap-polygon
                        v-for="(polygon, index) in polygons"
                        :vid="index" :ref="`polygon_${index}`"
                        :path="polygon.path"
                        :draggable="polygon.draggable"
                        :editable="polygon.editable"
                        :strokeOpacity="polygon.strokeOpacity"
                        fillOpacity="0.5"
                        strokeWeight="1"
                        strokeColor="#ff0000"
                        strokeStyle="dashed"
                        :events="polygon.events">

                </el-amap-polygon>
                <el-amap-marker v-for="marker in markers" :position="marker" ></el-amap-marker>
            </el-amap>
        </div>
    </el-dialog>
</template>
<script>
    import VueAMap from "vue-amap";
    import ElButton from "../../../../node_modules/element-ui/packages/button/src/button.vue";
    import TempSearchCity   from  '../detail/OperationRight.vue'
    import longitude  from '../../../assets/js/common/longitude.js'
    let amapManager = new VueAMap.AMapManager();
    export default {
        data() {
            let self = this;
            return {
                amapManager,
                zoom: 12,
                address:'',
                center: [106.239869, 38.471472],
                events: {
                    click(e) {
                        let { lng, lat } = e.lnglat;
                        self.lng = lng;
                        self.lat = lat;
                        self.polygons[0].path.push([lng,lat]);
                        self.coordinateSet.push([lng,lat]);
                        // 这里通过高德 SDK 完成。
                        var geocoder = new AMap.Geocoder({
                            radius: 1000,
                            extensions: "all"
                        });
                        geocoder.getAddress([lng ,lat], function(status, result) {
                            if (status === 'complete' && result.info === 'OK') {
                                if (result && result.regeocode) {
                                    self.address = result.regeocode.formattedAddress;
                                    self.$nextTick();
                                }
                            }
                        });
                    }
                },
                lng: 0,
                lat: 0,
                polygons: [
                    {
                        editable:true,
                        draggable: false,
                        strokeOpacity:0.5,
                        path:[],
                        events: {
                        }
                    }
                ],
                plugin: ['ToolBar', {
                    pName: 'MapType',
                    defaultType: 0,
                    events: {
                        init(o) {
                            console.log(o);
                        }
                    }
                }],
                markers: [
                ],
                searchOption: {
                    city: '',
                    citylimit: false
                },
                coordinateSet:[]
            }
        },
        props:{
            title:String,
            coordinateDialogVisible:Boolean,
            coordinateList:String
        },
        watch:{
            coordinateList(data){
                let vm = this;
                console.log(data.split(';'));
                data.split(';').forEach((item,val) => {
                    this.markers.push([longitude.changeToDFM(item.split(',')[0]),longitude.changeToDFM(item.split(',')[1])])
                })
            }
        },
        mounted(){

        },
        methods:{
            handleClose(done){
                let vm = this;
                let array = [];
                vm.$emit('coordinateDialogVisibleStatus',false);
                if(vm.coordinateSet.length <= 1) return;
                vm.coordinateSet.forEach((val,index) => {
                    array.push([
                        longitude.changeToDu(val[0]),longitude.changeToDu(val[1])
                    ])
                });
                vm.$emit('coodinateSet',array)
            },
            handleRefreshPath(){
                this.polygons[0].path = []
            },
            onSearchResult(pois) {
                let latSum = 0;
                let lngSum = 0;
                if (pois.length > 0) {
                    pois.forEach(poi => {
                        let {lng, lat} = poi;
                        lngSum += lng;
                        latSum += lat;
                        this.markers.push([poi.lng, poi.lat]);
                    });
                    let center = {
                        lng: lngSum / pois.length,
                        lat: latSum / pois.length
                    };
                    this.center = [center.lng, center.lat];
                }else{
                    this.markers = []
                }
            },
        },
        components: {
            ElButton,
            TempSearchCity

        }
    }
</script>
