import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/index/Index.vue'
import Index from './views/home/Home.vue'
import Project from './views/project/list/Project.vue'
import ProjectDetail from './views/project/detail/ProjectDetail.vue'
import ProjectArchive from './views/project/list/ProjectArchive.vue'
import Device from  './views/device/list/DeviceList.vue'
import DetailDetail from './views/device/detail/DeviceDetail.vue'
import SystemMonitor  from './views/monitor/SystemMonitor.vue'

Vue.use(Router);

export default new Router({
    routes: [{
        path: '/',
        name: 'login',
    },
        {
            path: '/index',
            component: Home,
            redirect: '/index/Default', //改变指向
            children: [{
                path: 'Default', //首页
                component: () => {
                    return import('./views/index/Default.vue')
                }
            }, {
                path: 'Products', //产品中心
                component: () => {
                    return import('./views/index/Products.vue')
                }
            }, {
                path: 'Scheme', //解决方案
                component: () => {
                    return import('./views/index/Scheme.vue')
                }
            }, {
                path: 'Helper', //帮助中心
                component: () => {
                    return import('./views/index/Helper.vue')
                }
            }]
        },
        {
            path: '/home',
            name: 'home',
            component: Index,
            redirect: '/project',
            children: [
                {
                    path: '/project',              //项目列表
                    name: 'project',
                    component: Project,
                },
                {
                    path: '/project/detail/:id',   //项目详情
                    name: 'projectDetail',
                    component: ProjectDetail,
                },
                {
                    path: '/project/archive/:id',  //工程档案
                    name: 'projectArchive',
                    component: ProjectArchive,
                },
                {
                    path: '/device',               //工程列表
                    name: 'device',
                    component: Device,
                },
                {
                    path: '/device/detail/:id',    //工程详情
                    name: 'deviceDetail',
                    component: DetailDetail,
                },
                {
                    path: '/system/monitor',       //系统监控
                    name: 'systemMonitor',
                    component: SystemMonitor,
                },
                {
                    name:'protocol',               //协议体系
                    path:'/protocol',
                    component:() => {
                        return import ('./views/protocol/Protocol.vue')
                    }
                },{
                    name:'protocolDetail',         //协议体系详情
                    path:'/protocol/detail/:id',
                    component:() => {
                        return import ('./views/protocol/ProtocolDetail.vue')
                    }
                },{
                    name:'typeDictionary',         //类型字典
                    path:'/typeDic',
                    component:() => {
                        //TODO
                        return import ('./views/protocol/TypeDictionary.vue')
                    }
                },{
                    name:'elementDictionary',      //元素字典
                    path:'/elementDic',
                    component:() => {
                        //TODO
                        return import ('./views/protocol/ElementDictionary.vue')
                    }
                },{
                    name:'pluginLibrary',          //插件库
                    path:'/plugin',
                    component:() => {
                        //TODO
                        return import ('./views/protocol/PlugInLibrary.vue')
                    }
                },{
                    name:'commandDictionary',      //命令字典
                    path:'/commandDic',
                    component:() => {
                        //TODO
                        return import ('./views/protocol/CommandDictionary.vue')
                    }
                }
            ]
        }
    ]
})