<template>
    <el-dialog
            :title="title"
            :visible.sync="coordinateCenterPointVisible"
            width="100%"
            class="general__dialog"
            top="5vh"
            append-to-body
            :before-close="handleClose">
        <div class="amap-page-head" style="">
            <el-amap-search-box
                    class="search-box"
                    :search-option="searchOption"
                    default="搜索中心点"
                    :on-search-result="onSearchResult">
            </el-amap-search-box>
        </div>
        <div class="amap-page-container">
            <el-amap
                    ref="map"
                    vid="amapDemo1"
                    :amap-manager="amapManager"
                    :center="center" :zoom="zoom"
                    :plugin="plugin"
                    :events="events"
                    class="amap-demo"
            >
                <el-amap-info-window
                        :position="windows[0].position"
                        :content="windows[0].content"
                        :visible="windows[0].visible"
                        :events="windows[0].events">
                </el-amap-info-window>
                <el-amap-polygon v-for="(polygon, index) in polygons" :vid="index" :ref="`polygon_${index}`" :path="polygon.path" :draggable="polygon.draggable" :events="polygon.events"></el-amap-polygon>
            </el-amap>
        </div>
    </el-dialog>
</template>
<script>
    import VueAMap from "vue-amap";
    import ElButton from "../../../../node_modules/element-ui/packages/button/src/button.vue";
    import TempSearchCity   from  '../detail/OperationRight.vue'
    import longitude  from '../../../assets/js/common/longitude.js'
    let amapManager = new VueAMap.AMapManager();
    export default {
        data() {
            let self = this;
            return {
                amapManager,
                zoom: 12,
                address:'',
                center: [106.239869, 38.471472],
                events: {
                    click(e) {
                        let { lng, lat } = e.lnglat;
                        self.lng = lng;
                        self.lat = lat;
                        self.center  = [lng,lat];
                        self.windows[0].position = [lng,lat];
                        // 这里通过高德 SDK 完成。
                        var geocoder = new AMap.Geocoder({
                            radius: 1000,
                            extensions: "all"
                        });
                        geocoder.getAddress([lng ,lat], function(status, result) {
                            if (status === 'complete' && result.info === 'OK') {
                                if (result && result.regeocode) {
                                    self.address = result.regeocode.formattedAddress;
                                    self.windows[0].content = result.regeocode.formattedAddress;
                                    self.$nextTick();
                                }else{

                                }
                            }else{
                                self.windows[0].content = '暂无地址信息';
                            }
                        });
                    }
                },
                windows: [
                    {
                        position: [106.239869, 38.471472],
                        content: '宁夏回族自治区银川市金凤区上海西路街道上海西路',
                        visible: true,
                        events: {
                            close() {
//                                self.windows[0].position = [106.239869, 38.471472];
//                                self.windows[0].
                            }
                        }
                    }
                ],
                lng: 0,
                lat: 0,
                polygons: [
                    {
                        draggable: true,
                        path:[],
                        events: {}
                    }
                ],
                plugin: ['ToolBar', {
                    pName: 'MapType',
                    defaultType: 0,
                    events: {
                        init(o) {
                            console.log(o);
                        }
                    }
                }],
                markers: [
                ],
                searchOption: {
                    city: '',
                    citylimit: false
                },
                currentWindow: {
                    position: [0, 0],
                    content: '',
                    events: {},
                    visible: false
                }
            }
        },
        props:{
            title:String,
            coordinateCenterPointVisible:Boolean,
            east:String,
            north:String
        },
        watch:{
            east(data){
                this.center[0] = longitude.changeToDFM(data);
            },
            north(data){
                this.center[1] = longitude.changeToDFM(data);
            }
        },
        mounted(){
//            this.currentWindow = this.windows[0];
        },
        methods:{
            handleClose(done){
                this.$emit('coordCPVStatus',false);
                this.$emit('coodData',{
                    x:longitude.changeToDu(this.center[0]),
                    y:longitude.changeToDu(this.center[1])
                })
            },
            handleRefreshPath(){
                this.polygons[0].path = []
            },
            onSearchResult(pois) {
                let latSum = 0;
                let lngSum = 0;
                if (pois.length > 0) {
                    pois.forEach(poi => {
                        let {lng, lat} = poi;
                        lngSum += lng;
                        latSum += lat;
                        this.markers.push([poi.lng, poi.lat]);
                    });
                    let center = {
                        lng: lngSum / pois.length,
                        lat: latSum / pois.length
                    };
                    this.center = [center.lng, center.lat];
                }else{
                    this.markers = []
                }
            },
        },
        components: {
            ElButton,
            TempSearchCity

        }
    }
</script>
